import React from 'react';






export default function Profile({profileTags}) {
    return (
        <div>

            <ul>
                <li><img src={profileTags.avatar}></img> </li>
                <li><p>User name</p>{profileTags.name}</li>
                <li><p>User location</p>{profileTags.location} </li>
            </ul>

        </div>
    )
}
